header {
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
}

.header_container {
    text-align: center;
    height: 100%;
    position: relative;
}

/*========CTA========*/
.cta {
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
    
}

/*========Header Socials========*/

.header_socials{
    
    display: flex;
    flex-direction: column;
    gap:0.8 rem;
    align-items: center;
    left: 0;
    position: absolute;
    bottom: 3rem;
}